export default {
  state: {
    infonum: 0,
    goodssum: 0,
    goodslists: [
    ],
    checkedGoods: [],
    dingdan: []
  },
  mutations: {
    add(state, obj) {
      // id
      let index = state.goodslists.findIndex(value => value.id == obj.id);
      if (index == -1) {
        state.goodslists.push(obj);
        state.checkedGoods = state.goodslists.map(value => value.id);
      } else {
        state.goodslists[index].num += 1;
      }
    }
  }
};
